/* raleway-regular - latin */
@font-face {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 400;
  src: url('font-files/raleway-v17-latin-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('font-files/raleway-v17-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('font-files/raleway-v17-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('font-files/raleway-v17-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('font-files/raleway-v17-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('font-files/raleway-v17-latin-regular.svg#Raleway') format('svg'); /* Legacy iOS */
}
